.App{
  width: 100vw;
  height: 100vh;
}
.cuadroCelu{
  border-radius: 50px;
  background-color: rgb(234, 236, 183);
  height: 100%;
  box-shadow: 0px 0px 20px 0px #000;
}
.titulo{
  text-align: center;
  padding: 20px 0px;
  background: #222;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  color: #eee;
}


@media screen and (max-width: 500px) {
  .cuadroCelu{
    border-radius: 0;
  }
  .titulo{
    border-radius: 0;
  }
}